@use '../../styles' as *;

.card {
  @extend %p-3x;
  @extend %column;

  gap: get-grid-space(2x);
}

.header {
  @extend %row;

  gap: get-grid-space(2x);
}

.info {
  @extend %column;

  width: 100%;
  gap: get-grid-space(1x);
}

.name {
  @extend %title-level-1;

  &::first-letter {
    text-transform: capitalize;
  }
}

.status {
  @extend %body-large;
}

.description {
  @extend %body-medium;
  color: $txt-secondary;

  &::first-letter {
    text-transform: capitalize;
  }
}

.statistics {
  @extend %column;
  gap: get-grid-space(1x);
}

.statistic {
  @extend %row;
}

.label {
  @extend %body-medium;
  flex: 1,
}

.relative {
  @extend %px-1x;
  @extend %row;
  @extend %body-medium;
  position: relative;
  background-color: $bg-secondary;
  width: 80%;
  height: get-grid-space(3x);
  margin-left: auto;
  border-radius: get-grid-space(3x);
}

.percentile {
  position: absolute;
  background-color: $bg-action-secondary;
  top: 0;
  left: 0;
  bottom: 0;
  border-radius: get-grid-space(2x);
}

.absolute {
  position: absolute;
}

.contacts {
  @extend %row;
  gap: get-grid-space(1x);

  .share {
    margin-left: auto;
  }
}
