// @todo Define grid system
@use "sass:math";
@use "sass:map";
@use "sass:list";
@use "sass-rem" as rem;

$browser-context: 16;

@function rem($pixels, $context: $browser-context) {
  @return rem.convert($pixels * 1px);
}

$grid-space: math.div($browser-context, 2);

$grid-spaces: (
  0x: 0px,
  quarterX: rem.convert(math.div($grid-space, 4)),
  halfX: rem.convert($grid-space * 0.5px),
  1x: rem.convert($grid-space * 1px),
  2x: rem.convert($grid-space * 2 * 1px),
  3x: rem.convert($grid-space * 3 * 1px),
  4x: rem.convert($grid-space * 4 * 1px),
  5x: rem.convert($grid-space * 5 * 1px),
  6x: rem.convert($grid-space * 6 * 1px),
  7x: rem.convert($grid-space * 7 * 1px),
  8x: rem.convert($grid-space * 8 * 1px),
);

@function get-grid-space($grid-size) {
  @return map.get($grid-spaces, $grid-size);
}

$paddings: (
  m: "margin",
  p: "padding",
);
$directions: (
  t: "top",
  b: "bottom",
  l: "left",
  r: "right",
);

@function get-grid-space-name($padding-type,
  $direction-type,
  $spacer-type,
  $negative: false) {
  $negative-prefix: "";

  @if $negative ==true {
    $negative-prefix: "n";
  }

  @if $direction-type ==all {
    @return #{$padding-type}-#{$negative-prefix}#{$spacer-type};
  }

  @else {
    @return #{$padding-type}#{$direction-type}-#{$negative-prefix}#{$spacer-type};
  }
}

@mixin get-grid-space($padding-type,
  $direction-type,
  $spacer-type,
  $negative: false) {
  $padding-value: map.get($paddings, $padding-type);
  $direction-value: map.get($directions, $direction-type);
  $space-value: map.get($grid-spaces, $spacer-type);
  $selector: get-grid-space-name($padding-type,
      $direction-type,
      $spacer-type,
      $negative );

  @if $negative ==true {
    $space-value: -$space-value;
  }

  @if $direction-type ==y {
    %#{$selector} {
      #{$padding-value}-top: $space-value;
      #{$padding-value}-bottom: $space-value;
    }
  }

  @else if $direction-type ==x {
    %#{$selector} {
      #{$padding-value}-left: $space-value;
      #{$padding-value}-right: $space-value;
    }
  }

  @else if $direction-type ==all {
    %#{$selector} {
      #{$padding-value}: $space-value;
    }
  }

  @else {
    %#{$selector} {
      #{$padding-value}-#{$direction-value}: $space-value;
    }
  }
}

$-directions: t, b, l, r, x, y, all;
$-negatives: true, false;

@each $direction in $-directions {
  @each $prop in map.keys($paddings) {
    @each $is-negative in $-negatives {
      @each $size in map.keys($grid-spaces) {
        @include get-grid-space($prop, $direction, $size, $is-negative);
      }
    }
  }
}