%view {
  @extend %p-2x;

  min-width: rem(1280);
  max-width: rem(1920);
  margin: 0 auto;
  display: grid;
  gap: get-grid-space(3x);
  grid-template-columns: repeat(12, 1fr);

  aside {
    grid-column: 1 / 5;
  }

  >section {
    grid-column: 5 / 13;
  }
}

%row {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%
}

%column {
  display: flex;
  flex-direction: column;
}
